import { getStocktimebar as _getStocktimebar, getStockInfoById as _getStockInfoById, getStockdaybar as _getStockdaybar, getStockweekbar as _getStockweekbar, getStockmonthbar as _getStockmonthbar, getEchnicalaspect as _getEchnicalaspect, getStockBid as _getStockBid } from "@/request/stock.js";
import { initMOption } from "@/assets/js/timeline.js";
import { makeOption, getOption } from "@/assets/js/dayline.js";
import BackTest from "./components/BackTest.vue";
export default {
  name: "StockDetail",
  components: {
    BackTest: BackTest
  },
  data: function data() {
    return {
      stockInfo: {},
      amount: "",
      amplitude: "",
      close: "",
      high: "",
      low: "",
      open: "",
      preClose: "",
      c: "",
      stockName: "",
      turnOverrate: "",
      upDownPrices: "",
      upDownRange: "",
      volume: "",
      totalMarketValue: "",
      flowMarketValue: "",
      comparison: "",
      totalFlowShares: "",
      stockList: [],
      activeName: "first",
      stockDayDataSave: [],
      stockWeekDataSave: [],
      stockMonthDataSave: [],
      echarts: {},
      kChart: {},
      prosperityLevel: "",
      market: "",
      abnormalaction: "",
      sellPrice5: "",
      sellPrice4: "",
      sellPrice3: "",
      sellPrice2: "",
      sellPrice1: "",
      boughtPrice1: "",
      boughtPrice2: "",
      boughtPrice3: "",
      boughtPrice4: "",
      boughtPrice5: "",
      sellCount5: "",
      sellCount4: "",
      sellCount3: "",
      sellCount2: "",
      sellCount1: "",
      boughtCount1: "",
      boughtCount2: "",
      boughtCount3: "",
      boughtCount4: "",
      boughtCount5: "",
      isShowZB: false
    };
  },
  created: function created() {
    this.echarts = require("echarts");
    this.getStockInfoById(this.stockNum);
    this.getEchnicalaspect(this.stockNum);
  },
  mounted: function mounted() {
    var dom = document.getElementById("k-chart");
    this.kChart = this.echarts.init(dom);
    this.getStocktimebar(this.stockNum);
    this.getStockBid(this.stockNum);
  },
  computed: {
    stockNum: {
      get: function get() {
        return String(this.$route.params.stockNum);
      },
      set: function set(v) {}
    } // stockNum() {
    //   return String(this.$route.params.stockNum);
    // },

  },
  methods: {
    getEchnicalaspect: function getEchnicalaspect(stockNum) {
      var _this = this;

      _getEchnicalaspect({
        stockNum: stockNum
      }).then(function (res) {
        var rawData = res;

        if (rawData.prosperityLevel == 1) {
          _this.prosperityLevel = "极弱，建议买入";
        } else if (rawData.prosperityLevel == 2) {
          _this.prosperityLevel = "弱，建议观望";
        } else if (rawData.prosperityLevel == 3) {
          _this.prosperityLevel = "强，建议买入";
        } else if (rawData.prosperityLevel == 4) {
          _this.prosperityLevel = "极强，建议卖出";
        }

        if (rawData.bullMarket == 1) {
          _this.market = "多头市场，以持股为主要策略，操作为高抛或减仓，落袋为安";
        } else if (rawData.bearMarket == 1) {
          _this.market = "空头市场，以持币为主要策略，操作为低吸，等待下一波上涨行情";
        } else {
          _this.market = "暂无明确趋势，建议观望为主";
        }
      });
    },
    changeOptionType: function changeOptionType(indicatorType) {
      this.kChart.setOption(getOption(this.kChart, this.stockDayDataSave, indicatorType), true);
    },
    tabClick: function tabClick(tab) {
      if (tab.name == "first") {
        this.getStocktimebar(this.stockNum);
        this.isShowZB = false;
      } else if (tab.name == "second") {
        this.getStockdaybar(this.stockNum);
        this.isShowZB = true;
      } else if (tab.name == "third") {
        this.getStockweekbar(this.stockNum);
        this.isShowZB = true;
      } else {
        this.getStockmonthbar(this.stockNum);
        this.isShowZB = true;
      }
    },
    getStocktimebar: function getStocktimebar(searchText) {
      var _this2 = this;

      var data = {
        stockNum: searchText
      };

      _getStocktimebar(data).then(function (res) {
        // var echarts = require('echarts')
        _this2.kChart.setOption(initMOption(res.reverse(), "hs"), true);
      });
    },
    getStockdaybar: function getStockdaybar(searchText) {
      var _this3 = this;

      var data = {
        stockNum: searchText
      };

      var _this = this;

      _getStockdaybar(data).then(function (res) {
        _this.stockDayDataSave = res.reverse();
        var VOLUMN_TYPE = 1;

        _this3.kChart.setOption(makeOption(res, VOLUMN_TYPE), true);
      });
    },
    getStockweekbar: function getStockweekbar(searchText) {
      var _this4 = this;

      var data = {
        stockNum: searchText
      };

      var _this = this;

      _getStockweekbar(data).then(function (res) {
        _this.stockWeekDataSave = res.reverse();
        var VOLUMN_TYPE = 1;

        _this4.kChart.setOption(makeOption(res, VOLUMN_TYPE), true);
      });
    },
    getStockmonthbar: function getStockmonthbar(searchText) {
      var _this5 = this;

      var data = {
        stockNum: searchText
      };

      var _this = this;

      _getStockmonthbar(data).then(function (res) {
        _this.stockMonthDataSave = res.reverse();
        var VOLUMN_TYPE = 1;

        _this5.kChart.setOption(makeOption(_this5.stockMonthDataSave, VOLUMN_TYPE), true);
      });
    },
    getStockInfoById: function getStockInfoById(stockid) {
      var _this6 = this;

      var data = {
        stockId: stockid
      };

      var _this = this;

      _getStockInfoById(data).then(function (res) {
        var rawData = res;
        _this.stockInfo = rawData;
        _this.amount = "成交额：" + rawData.amount;
        _this.amplitude = "振幅：" + (rawData.amplitude * 100).toFixed(2) + "%";
        _this.close = rawData.close;
        _this.high = "最高：" + rawData.high;
        _this.low = "最低：" + rawData.low;
        _this.open = "今开：" + rawData.open;
        _this.preClose = "昨收：" + rawData.preClose;
        _this.stockNum = rawData.stockNum;
        _this.stockName = rawData.stockName;
        _this.turnOverrate = "换手率：" + (rawData.turnOverrate * 100).toFixed(2) + "%";
        _this.upDownPrices = rawData.upDownPrices;
        _this.upDownRange = rawData.upDownRange;
        _this.volume = "成交量：" + _this6.formatNum(rawData.volume) + "手";
        _this.totalMarketValue = "总市值：" + _this6.formatNum(rawData.totalMarketValue);
        _this.flowMarketValue = "流通市值：" + _this6.formatNum(rawData.flowMarketValue);
        _this.totalFlowShares = "流通股本：" + _this6.formatNum(rawData.totalFlowShares);
      });
    },
    formatNum: function formatNum(num) {
      if (num > 100000000) {
        return (num / 100000000).toFixed(2) + "亿";
      } else if (num > 10000) {
        return (num / 10000).toFixed(2) + "万";
      } else {
        return num;
      }
    },
    getStockBid: function getStockBid(stockNum) {
      var data = {
        stockNum: stockNum
      };

      var _this = this;

      _getStockBid(data).then(function (res) {
        var rawData = res;
        _this.sellPrice5 = rawData.sellPrice5;
        _this.sellPrice4 = rawData.sellPrice4;
        _this.sellPrice3 = rawData.sellPrice3;
        _this.sellPrice2 = rawData.sellPrice2;
        _this.sellPrice1 = rawData.sellPrice1;
        _this.boughtPrice1 = rawData.boughtPrice1;
        _this.boughtPrice2 = rawData.boughtPrice2;
        _this.boughtPrice3 = rawData.boughtPrice3;
        _this.boughtPrice4 = rawData.boughtPrice4;
        _this.boughtPrice5 = rawData.boughtPrice5;
        _this.sellCount5 = rawData.sellCount5;
        _this.sellCount4 = rawData.sellCount4;
        _this.sellCount3 = rawData.sellCount3;
        _this.sellCount2 = rawData.sellCount2;
        _this.sellCount1 = rawData.sellCount1;
        _this.boughtCount1 = rawData.boughtCount1;
        _this.boughtCount2 = rawData.boughtCount2;
        _this.boughtCount3 = rawData.boughtCount3;
        _this.boughtCount4 = rawData.boughtCount4;
        _this.boughtCount5 = rawData.boughtCount5;
        var totalBought = rawData.boughtCount1 + rawData.boughtCount2 + rawData.boughtCount3 + rawData.boughtCount4 + rawData.boughtCount5;
        var totalSell = rawData.sellCount1 + rawData.sellCount2 + rawData.sellCount3 + rawData.sellCount4 + rawData.sellCount5;
        console.log(totalBought);

        if (isNaN(totalSell)) {
          totalSell = 0;
        }

        _this.comparison = "委比：" + ((totalBought - totalSell) / (totalBought + totalSell) * 100).toFixed(2) + "%";
      });
    }
  }
};